import React, { useContext } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { NavLink } from 'react-router-dom';
import { AppContext } from './AppContext';
import './NavBar.css';


function NavBar() {
  const { isDarkMode, toggleDarkMode } = useContext(AppContext);

  const handleChange = () => {
    toggleDarkMode();
    document.body.style.background = isDarkMode
  ? 'linear-gradient(to bottom, #3498db, #ffc8dd)'
  : 'linear-gradient(to bottom, #f94892, #0f2435)';
    updateStyles(isDarkMode);
  };

  const updateStyles = (isDarkMode) => {
    const navbar = document.querySelector('.navbar');
    const logo = document.querySelector('.logo');
    const links = document.querySelectorAll('.l');
    const toggleButton = document.querySelector('.navbar-toggler');

    if (isDarkMode) {
      navbar.style.backgroundColor = '#3498db';
      logo.style.backgroundColor = '#ffc8dd';
      logo.style.color = 'transparent';
      links.forEach(link => {
  link.style.color = '#ffc8dd';
  link.style.borderColor = '#ffc8dd';
      });
      toggleButton.classList.add('btn-dark');
    } else {
      navbar.style.backgroundColor = '#0f2435';
      logo.style.backgroundColor = '#ffc8dd';
      logo.style.color = 'transparent';
      links.forEach(link => {
        link.style.color = '#ffc8dd';
        link.style.borderColor = '#ffc8dd';
      });
      toggleButton.classList.remove('btn-dark');
    }
  };

  return (
    <Navbar expand="lg" className="navbar">
      <Container>
        <Navbar.Brand href="/CC1_react" className='logo'>CC1_React</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" className="btn-light" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mx-auto">
            <Nav.Link as={NavLink} to='/TP1' className='l'>TP1</Nav.Link>
            <Nav.Link as={NavLink} to='/TP2' className='l'>TP2</Nav.Link>
            <Nav.Link as={NavLink} to='/TP3' className='l'>TP3</Nav.Link>
            <Nav.Link as={NavLink} to='/TP4' className='l'>TP4</Nav.Link>
          </Nav>
          <div className="form-check-label form-switch">
            <input  className="form-check-input" onChange={handleChange}  type="checkbox"
              checked={isDarkMode}
            />
          </div>

        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBar;
